import logo from './logo.svg';
import React from 'react'
import './App.css';
import 'tui-image-editor/dist/tui-image-editor.css';
import { fabric } from 'fabric';

/*Boostrap Stuff*/
import Form from 'react-bootstrap/Form'
import Collapse from 'react-bootstrap/Collapse'
import { Button, InputGroup } from 'react-bootstrap';
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'

/*Font Awesome stuff*/
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { library } from '@fortawesome/fontawesome-svg-core'
import { fas } from "@fortawesome/free-solid-svg-icons";
library.add(fas)

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      canvas: null,
      hueSet: false,
      hueValue: 0,

      rgbSet: false,
      r: 0,
      g: 0,
      b: 0,

      brightnessSet: false,
      brightnessValue: 0,

      hasSelect: false,
      listFileName: [],
    }
  }


  componentDidMount = () => {
    var canvas = new fabric.Canvas('canvas', {
      width: '600',
      height: '600',
      backgroundColor: 'lightgray'
    })
    canvas.on('selection:created', (param) => {
      const filter = param.selected[0].filters
      if (typeof filter[0] !== "undefined") {
        this.setState({ hueSet: filter[0].isSet, hueValue: filter[0].rotation })
      }
      if (typeof filter[1] !== "undefined") {
        this.setState({ rgbSet: filter[1].isSet, r: filter[1].gamma[0], g: filter[1].gamma[1], b: filter[1].gamma[2] })
      }
      this.setState({ hasSelect: true })
    })
    canvas.on('selection:cleared', (parama) => {
      this.setState({ hasSelect: false })
    })
    canvas.renderAll();
    this.setState({ canvas: canvas });
    window.onkeydown = this.onKeyDownHandler;
  }

  onKeyDownHandler = (e) => {
    switch (e.keyCode) {
      case 8: // delete
        var object = this.state.canvas.getActiveObject();
        this.state.canvas.remove(object);
    }
    e.preventDefault();
  }

  onClickAddImage = (e) => {
    const reader = new FileReader()
    const imageFile = e.target.files[0]
    reader.readAsDataURL(imageFile)
    reader.onloadend = () => {
      fabric.Image.fromURL(reader.result, img => {
        this.state.canvas.add(img);
        this.state.canvas.requestRenderAll();
      })
    }
  }

  onChangeApplyFilter = (filter) => {
    var obj = this.state.canvas.getActiveObject();
    if (obj) {
      switch (filter) {
        case 'hue':
          obj.filters[0] = this.state.hueSet && new fabric.Image.filters.HueRotation({ rotation: this.state.hueValue, isSet: true })
          break;
        case 'rgb':
          const { r, g, b } = this.state
          obj.filters[1] = this.state.rgbSet && new fabric.Image.filters.Gamma({ gamma: [r, g, b], isSet: true })
          break;
        case 'brightness':
          obj.filters[2] = this.state.brightnessSet && new fabric.Image.filters.Brightness({ brightness: this.state.brightnessValue , isSet: true })
          break;
        default:
      }
      obj.applyFilters()
      this.state.canvas.requestRenderAll();
    }
  }

  onChangeHandlerCheckBox = async (e, filter) => {
    await this.setState({ [e.target.name]: e.target.checked })
    this.onChangeApplyFilter(filter)
  }

  onChangeHandlerRange = (e, filter) => {
    this.setState({ [e.target.name]: e.target.value })
    this.onChangeApplyFilter(filter)
  }

  render = () => {
    const { hueSet, hueValue, rgbSet, r, g, b, brightnessSet, brightnessValue } = this.state

    return (
      <div className="App">
        <canvas className="image-editor" id="canvas"></canvas>
        <div className="editor">
          <div className="card d-flex flex-row">
            <div className="input-group cursor-pointer">
              <div className="custom-file">
                <div className="input-group-prepend">
                  <Button className="btn-common-size btn-sm" variant="primary"><FontAwesomeIcon className="mr-1 " icon={["fas", "upload"]} />Upload</Button>
                </div>
                <input
                  type="file"
                  className="custom-file-input"
                  id="inputGroupFile01"
                  aria-describedby="inputGroupFileAddon01"
                  onChange={this.onClickAddImage}
                />
              </div>
            </div>
            <div className="d-flex flex-column text-info">
              {this.state.listFileName.forEach(row => {
                <div>{this.state.listFileName}</div>
              })}
            </div>
            {/*     <input className="outline-primary" type="file" accept="image/*" onChange={this.onClickAddImage}/> */}
          </div>

          <Accordion defaultActiveKey="0">
            <div className="card">
              <Accordion.Toggle as={Button} eventKey="0" variant="light" className="card-header d-flex justify-content-between align-items-center px-3 py-0">
                <label className="mr-2 font-weight-bold">Canvas Size</label>
              </Accordion.Toggle>
              <Accordion.Collapse eventKey="0">
                <div className="card-body py-2">
                  <div className="d-flex justify-content-between my-2">
                    <span>Width</span>
                    <div className="input-group input-group-sm" style={{ "width": "7rem" }}>
                      <input type="text" className="form-control" />
                      <div className="input-group-append">
                        <span className="input-group-text">px</span>
                      </div>
                    </div>
                  </div>
                  <div className="d-flex justify-content-between">
                    <span>Height</span>
                    <div className="input-group input-group-sm" style={{ "width": "7rem" }}>
                      <input type="text" className="form-control" />
                      <div className="input-group-append">
                        <span className="input-group-text">px</span>
                      </div>
                    </div>
                  </div>
                  <div className="d-flex justify-content-end pt-3">
                    <button type="button" className="btn btn-sm btn-primary border">Apply</button>
                  </div>
                </div>
              </Accordion.Collapse>
            </div>
          </Accordion>

          {/* After Selected */}
          {this.state.hasSelect ?
            <>
            <Accordion defaultActiveKey="0">
                <div className="card">
                  <Accordion.Toggle as={Button} eventKey="0" variant="light" className="card-header d-flex justify-content-between align-items-center px-3 py-0">
                    <label className="mr-2 font-weight-bold">Brightness</label>
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="0">
                    <div className="card-body py-2">
                      <div className="d-flex justify-content-end mb-2">
                        <input name="brightnessSet" type="checkbox" checked={brightnessSet} onChange={(e) => this.onChangeHandlerCheckBox(e, 'brightness')} />
                      </div>
                      <div className="d-flex justify-content-between">
                        <span className="mr-2">Brightness</span>
                        <input name="brightnessValue" className="range brightness" style={{ "width": "80%" }} value={brightnessValue} type="range" min="0" max="0.8" step="0.01" onChange={(e) => this.onChangeHandlerRange(e, 'brightness')} />
                      </div>
                    </div>
                  </Accordion.Collapse>
                </div>
              </Accordion>

              <Accordion defaultActiveKey="0">
              <div className="card">
                <Accordion.Toggle as={Button} eventKey="0" variant="light" className="card-header d-flex justify-content-between align-items-center px-3 py-0">
                  <label className="mr-2 font-weight-bold">Gamma</label>
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="0">
                  <div className="card-body py-2">
                    <div className="d-flex justify-content-end mb-2">
                      <input name="rgbSet" type="checkbox" checked={rgbSet} onChange={(e) => this.onChangeHandlerCheckBox(e, 'rgb')} />
                    </div>
                    <div className="d-flex justify-content-between">
                      <span className="mr-2">Red</span>
                      <input name="r" className="range red" style={{ "width": "80%" }} value={r} type="range" min="0.2" max="2.2" step="0.003921" onChange={(e) => this.onChangeHandlerRange(e, 'rgb')} />
                    </div>
                    <div className="d-flex justify-content-between">
                      <span className="mr-2">Green</span>
                      <input name="g" className="range green" style={{ "width": "80%" }} value={g} type="range" min="0.2" max="2.2" step="0.003921" onChange={(e) => this.onChangeHandlerRange(e, 'rgb')} />
                    </div>
                    <div className="d-flex justify-content-between">
                      <span className="mr-2">Blue</span>
                      <input name="b" className="range blue" style={{ "width": "80%" }} value={b} type="range" min="0.2" max="2.2" step="0.003921" onChange={(e) => this.onChangeHandlerRange(e, 'rgb')} />
                    </div>
                  </div>
                </Accordion.Collapse>
              </div>
            </Accordion>

              <Accordion defaultActiveKey="0">
                <div className="card">
                  <Accordion.Toggle as={Button} eventKey="0" variant="light" className="card-header d-flex justify-content-between align-items-center px-3 py-0">
                    <label className="mr-2 font-weight-bold">Hue</label>
                  </Accordion.Toggle>
                  <Accordion.Collapse eventKey="0">
                    <div className="card-body py-2">
                    <div className="d-flex justify-content-end mb-2">
                      <input name="hueSet" type="checkbox" checked={hueSet} onChange={(e) => this.onChangeHandlerCheckBox(e, 'hue')} />
                    </div>
                      <div className="d-flex justify-content-between">
                        <span className="mr-2">Hue</span>
                        <input name="hueValue" className="range hue" style={{ "width": "80%" }} value={hueValue} type="range" min="-2" max="2" step="0.002" onChange={(e) => this.onChangeHandlerRange(e, 'hue')} />
                      </div>
                    </div>

                  </Accordion.Collapse>
                </div>
              </Accordion>
            </>
            :
            <div className="p-3">Upload image for adjusting</div>
          }
        </div>
      </div>
    );
  }
}

export default App;
